<template>
  <el-row class="match-line-wrapper">
    <el-col :span="10">
      <div class="match-line-left">
        <a :href="'/owl/team/'+match.competitors[0].id+'/'+match.competitors[0].name" class="team-name">
          <span>{{showFull? match.competitors[0].name:match.competitors[0].abbreviatedName}}</span>
          <img :src="match.competitors[0].logo" class="team-logo-standing" :alt="match.competitors[0].name">
        </a>
      </div>
    </el-col>
    <el-col :span="4">
      <div class="match-score" v-if="match.state==='CONCLUDED'">
         <span class="score-span" :class="[match.winner?match.winner.id===match.competitors[0].id?'win':'lose':'']">
        {{match.scores[0].value}}
      </span>
        <span class="score-span" style="width: 1rem">
      </span>
        <span class="score-span" :class="[match.winner?match.winner.id===match.competitors[1].id?'win':'lose':'']">
        {{match.scores[1].value}}
      </span>
      </div>
      <div v-else class="match-time">
        {{new Date(match.startDate).toLocaleTimeString($owl.locale,{hour: '2-digit', minute:'2-digit',hour12:false})}}
      </div>
    </el-col>
    <el-col :span="10">
      <div class="match-line-right">
        <a :href="'/owl/team/'+match.competitors[1].id+'/'+match.competitors[1].name" class="team-name">
          <img :src="match.competitors[1].logo" class="team-logo-standing" :alt="match.competitors[1].name">
          <span>{{showFull? match.competitors[1].name:match.competitors[1].abbreviatedName}}</span>
        </a>
      </div>
    </el-col>
  </el-row>


</template>

<script>
  export default {
    name: "MatchBrief",
    props: ['match'],
    computed: {
      showFull() {
        return this.$store.getters.getWindowParam.width > 768;
      },
    }
  }
</script>

<style scoped>
  .match-line-wrapper {
    padding: 1rem 0;
    border-bottom: 1px solid rgba(51, 51, 51, .2);
  }

  .match-line-left {
    display: flex;
    justify-content: flex-end;

  }

  .match-line-right {
    display: flex;
  }

  .match-score {
    height: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;

  }

  .match-time {
    height: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    background-color: #f3f3f3;
    padding: 0.2rem;
    width: 80%;
    margin: 0 auto;

  }

  .win {
    color: green;
  }

  .lose {
    color: red;

  }

  .team-name {
    display: flex;
    align-items: center;
  }

  .team-logo-standing {
    width: 1.6rem;
    height: 1.6rem;
    margin: 0 0.3rem;
  }
</style>



