<template>
  <div>
    <el-tabs v-if="stages.length>0" type="border-card" value="stage1" class="tab-outside">
      <el-tab-pane :label="meta.strings['owl.schedule.stage.'+stage.slug]" :name="stage.slug" v-for="stage in stages"
                   :key="stage.id">
        <el-tabs type="border-card" :value="0" class="tab-inside">
          <el-tab-pane :label="'第'+(week.id+1)+'周'" :name="week.id+''"
                       v-for="week in stage.weeks"
                       :key="week.id">
            <div v-for="day in week.days" class="matches-day">
              {{day.startDay}} {{new Date(day.startDay).toLocaleString($owl.locale,{weekday: 'long'})}}
              <match-brief :match="match" v-for="match in day.matches" :key="match.id"></match-brief>

            </div>
          </el-tab-pane>

        </el-tabs>
      </el-tab-pane>

    </el-tabs>
    <div v-loading="stages.length===0" style="min-height: 300px"></div>
  </div>
</template>

<script>
  import MatchBrief from "@/components/Matches/MatchBrief";

  export default {
    name: "Schedule",
    components: {MatchBrief},
    data() {
      return {
        stages: [],
        totalData: {},
        meta: {}
      }
    },
    methods: {
      fetchMatches() {
        this.stages = [];
        this.$owl.schedule({season: this.season}).then(({stages, meta}) => {
          this.stages = stages;
          this.totalData[this.season] = stages;
          this.meta = meta;
        })
      }
    },
    mounted() {
      this.fetchMatches();
    },
    watch: {
      season: function () {
        if (this.totalData[this.season] === undefined)
          this.fetchMatches();
        else {
          this.stages = this.totalData[this.season];
        }

      }
    },
    computed: {
      season() {
        return this.$store.getters.getSeason
      },
    }
  }
</script>

<style>
  .matches-day {
    margin-bottom: 20px;
  }


</style>



